.projects {
  padding-top: 2em;
  background-color: rgba(11, 211, 14, 0.5);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  height: 40em;
}

.box {
  display: flex;
  position: relative;
  flex-direction: column;
  height: 180px;
  width: 10em;
  margin: 2em 0;
  min-width: 10em;
  border-radius: 2.5em;
  background-color: #000800;
  box-shadow: -1rem 0 3rem black;
  transition: 0.2s ease-in-out;
  cursor: pointer;
}

.box:hover {
  transform: translateY(-1rem);
  height: 350px;
}
.box_image {
  width: 10em;
  height: 10em;
  border-radius: 2.5em;
}
.content {
  position: relative;
  text-align: center;
  visibility: hidden;
  opacity: 0;
  transition: 0.2s ease-in-out;
}

.projects .box:hover .content {
  visibility: visible;
  opacity: 1;
  margin-top: 1em;
  transition-delay: 0.1s;
}
