.about {
  max-width: 100%;
  color: white;
  display: flex;
  flex-direction: column;
  text-align: center;
  background: linear-gradient(
    180deg,
    rgba(11, 211, 14, 0.5),
    rgba(11, 211, 14, 0)
  );
  padding: 4em;
}
.button {
  margin: 1em 0;
}

.hire_button {
  padding: 1em 2em;
  color: white;
  background: rgba(11, 211, 14, 0);
  border: 1px solid rgba(11, 211, 14, 0.5);
  transition: 0.2s;
  font-size: 1em;
  cursor: pointer;
}

.hire_button:hover {
  background: rgba(11, 211, 14, 0.5);
  border-radius: 1em;
}

.profile_body {
  margin-top: 20px;
}

.profile_image {
  border-radius: 50%;
  border: 4px double white;
  margin-bottom: 10px;
  width: 200px;
}

.profile_title {
  font-size: 2rem;
  line-height: 1;
  letter-spacing: 1px;
  border-bottom: 2px solid white;
}

.profile_position {
  text-transform: uppercase;
  font-size: 0.8rem;
  letter-spacing: 3px;
  margin-top: 20px;
}

@media (min-width: 600px) {
  .about {
    flex-direction: row;
    align-items: center;
    text-align: left;
  }
  .profile_detail {
    margin-left: 2em;
  }
  .profile_title::after {
    margin: 0;
  }
}
