.nav {
  color: white;
  font-size: 2.2vw;
  display: flex;
  justify-content: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 10;
  background: #000800;
}

@media screen and (min-width: 600px) {
  .nav {
    font-size: 15px;
  }
}

.nav_list_item {
  cursor: pointer;
  padding: 1em 2em;
  transition: 0.5s;
}
.nav_list_item:hover {
  background-color: #0bd30e;
  color: #000800;
}
