.progress {
  display: flex;
  transition: all 0.2s ease;
  flex-direction: column;
  width: 45%;
  margin-left: 2.5%;
  padding-left: 2.5%;
  padding-bottom: 2.5%;
}
.progressHovered {
  background-color: rgba(11, 211, 14, 0.5);
}

.progress_container {
  height: 10px;
  width: 90%;
  border: 1px solid white;
  display: flex;
  align-items: center;
}

.progress_filler {
  height: 60%;
  text-align: left;
  padding-left: 8px;
  background-color: #0bd30e;
  transition: 0.5s;
}

.progress_label {
  padding: 5;
  color: white;
  font-weight: bold;
  margin: 1em 0;

  transition: 0.5s;
}

.progress:hover {
  background-color: rgba(11, 211, 14, 0.5);
}

.progress:hover .progress_filler {
  background-color: white;
}
